function Projects(props) {

  const { data, hideTabs } = props;

  return (
    <>
      {
        !hideTabs &&
          <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
            <div className="col-md-12">
              <p className="work-menu">
                <span><a href="#" className="active">Angular</a></span>
                <span><a href="#">NodeJS</a></span>
              </p>
            </div>
          </div>
      }
      <div className="row">
        {
          data && data.length &&
          data.map((_work, _idx) => {
            return (
              <div className="col-md-6 animate-box item-animate" data-animate-effect={(_idx % 2 == 0)? 'fadeInLeft': 'fadeInRight'} key={_idx}>
                <div className="project" style={{backgroundImage: `url(/assets/images/projects/${_work.image})`}}>
                  <div className="desc">
                    <div className="con">
                      <h3><a href="work.html">{_work.name}</a></h3>
                      <span>{_work.description}</span>
                      <p className="icon">
                        {
                          _work.link && <span><a href={_work.link} target="_blank" rel="noreferer"><i className="icon-eye"></i> View</a></span>
                        }
                        {
                          _work.technologies &&
                            _work.technologies.map((_tech, _tidx) => {
                              return (
                                <span key={_tidx}><a>{_tech}</a></span>
                              )
                            })
                        }
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            )
          })
        }
        
      </div>
      {/* <div className="row">
        <div className="col-md-12 animate-box">
          <p><a href="#" className="btn btn-primary btn-lg btn-load-more">Load more <i className="icon-reload"></i></a>
          </p>
        </div>
      </div> */}
    </>			
  )
}

export default Projects;