
function Experiences(props) {

  const { data } = props;

  return (
    <div className="timeline-centered">
      {
        data && data.length &&
        data.map((_exp, _idx) => {
          return (
            <article className="timeline-entry animate-box item-animate" data-animate-effect={(_idx % 2 == 0)? 'fadeInLeft': 'fadeInRight'} key={_idx}>
              <div className="timeline-entry-inner">
                <div className={`timeline-icon color-${_idx + 1}`}>
                  <i className="icon-pen2"></i>
                </div>
                <div className="timeline-label">
                  <h2><a href="#">{_exp.title} in {_exp.company}</a> <span>({parseInt(_exp.startDate)}-{parseInt(_exp.endDate)})</span></h2>
                  <p>{_exp.description}</p>
                </div>
              </div>
            </article>
          )
        })
      }
      <article className="timeline-entry begin animate-box fadeInUp animated" data-animate-effect="fadeInBottom">
        <div className="timeline-entry-inner">
          <div className="timeline-icon color-none"></div>
        </div>
      </article>
    </div>
  )
}

export default Experiences;