
function Educations(props) {

  const { data } = props;

  return (
    <div className="panel-group" id="accordion" role="tablist" aria-multiselectable="true">
      {
        data && data.length &&
        data.map((_edu, _idx) => {
          return (
            <div className="panel panel-default" key={_idx}>
              <div className="panel-heading" role="tab" id={`ed-heading${_idx}`}>
                <h4 className="panel-title">
                  <a className={_idx == 0 ? '' : 'collapsed'} data-toggle="collapse" data-parent="#accordion" href={`#ed-collapse${_idx}`} aria-expanded={_idx == 0 ? 'true' : 'false'} aria-controls={`ed-collapse${_idx}`}>
                    {_edu.title}
                  </a>
                </h4>
              </div>
              <div id={`ed-collapse${_idx}`} className={`panel-collapse collapse ${_idx == 0 ? 'in' : ''}`} role="tabpanel" aria-labelledby={`ed-heading${_idx}`}>
                <div className="panel-body">
                  <div className="row">
                    <div className="col-md-8">
                      <p>
                        <b>{_edu.institute}</b>
                          &nbsp;
                        <span>({parseInt(_edu.startDate)} - {parseInt(_edu.endDate)}):</span>
                          &nbsp;
                        {_edu.description}
                      </p>
                      {
                        (_edu.points && _edu.points.length) ?
                          <ul className="list">
                            {
                              _edu.points.map((_point, _pidx) => {
                                return (
                                  <li key={_pidx}>{_point}</li>
                                )
                              })
                            }
                          </ul>
                          : ''
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
        })
      }
    </div>
  )
}

export default Educations;