function Skills(props) {
  const { data, description } = props;
  return (
    <div className="ipsingh-narrow-content">
      <div className="row">
        <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
          <span className="heading-meta">My Specialty</span>
          <h2 className="ipsingh-heading animate-box">My Skills</h2>
        </div>
      </div>
      <div className="row">
        <div className="col-md-12 animate-box" data-animate-effect="fadeInLeft">
          <p>{description}</p>
        </div>
        {
          data && data.length &&
          data.map((_skill, _idx) => {
            return (
              <div className="col-md-4 animate-box item-animate" data-animate-effect={(_idx % 2 == 0) ? 'fadeInLeft' : 'fadeInRight'} key={_idx}>
                <div className="progress-wrap">
                  <h3><img src={`/assets/icons/${_skill.slug}.png`} className="skill-img"/>{_skill.name}</h3>
                  <div className="progress">
                    <div className={`progress-bar color-${_idx} color-${_skill.slug}`} role="progressbar" aria-valuenow={_skill.percent} aria-valuemin="0" aria-valuemax="100" style={{ width: `${_skill.percent}%` }}>
                      <span>{_skill.percent}%</span>
                    </div>
                  </div>
                </div>
              </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default Skills;